
"use client";

import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { useAuth } from "./Auth/AuthProvider";
import { deleteDoc, doc } from "firebase/firestore";
import { db } from "@/ai/ai-instance";
import { useRouter } from "next/navigation";

interface Task {
  id: string;
  title: string; 
  description: string;
  location: string;
  budget: number;
  category: string;
  creatorId: string;
}

interface TaskDetailsProps {
  task: Task;
}



const TaskDetails = ({ task }: TaskDetailsProps) => {
  const { user } = useAuth();
  const router = useRouter();

  const handleAcceptTask = () => {
    alert(`Task "${task.title}" accepted!`);
  };

  const handleDeleteTask = async () => {
    const confirmed = window.confirm("Are you sure you want to delete this task?");
    if (confirmed) {
      try {
        await deleteDoc(doc(db, "tasks", task.id));
        alert("Task deleted successfully!");
        router.push("/");
      } catch (error) {
        console.error("Error deleting task:", error);
        alert("Failed to delete task.");
      }
    }
  };

  const handleEditTask = () => {
    // Placeholder for task editing logic
    router.push(`/edit-task/${task.id}`);
    console.log(`Editing task: ${task.id}`);
  };

  const isTaskOwner = () => {
    if (!user) {
      return false;
    }
    return user.uid === task.creatorId;
  };

  return (
    <div className="flex justify-center items-center py-10 bg-secondary">
      <Card className="w-full max-w-2xl">
        <CardHeader>
          <CardTitle className="text-2xl">{task.title}</CardTitle>
        </CardHeader>
        <CardContent className="grid gap-6">
          <CardDescription>{task.description}</CardDescription>
          <div className="grid gap-2">
            <p>Location: {task.location}</p>
            <p>Budget: ${task.budget}</p>
            <p>Category: {task.category}</p>
          </div>
          {isTaskOwner() ? (
            <div className="flex gap-2">
              <Button onClick={handleEditTask} className="w-1/2">
                Edit
              </Button>
              <Button onClick={handleDeleteTask} className="w-1/2 bg-red-500">
                Delete
              </Button>
            </div>
          ) : (
            <Button onClick={handleAcceptTask} className="w-full">Accept Task</Button>
          )}

        </CardContent>
      </Card>
    </div>
  );
};

export default TaskDetails;
